<template>
  <div class="payment-page container">
    <div class="page-header">
      <h1>钻石商店</h1>
      <p>购买钻石以解锁强大的 AI 服饰分析功能。每次分析仅消耗 1 颗钻石。</p>
    </div>

    <div v-if="loading" class="loading-state" v-loading="loading"></div>

    <div v-else class="packages-grid">
      <div v-for="pkg in packages" :key="pkg.id" :class="['package-card', { recommended: pkg.is_recommended }]" @click="selectPackage(pkg)">
        <div v-if="pkg.is_recommended" class="recommended-badge">推荐</div>
        <div class="package-icon"><i class="el-icon-s-opportunity"></i></div>
        <h3 class="package-name">{{ pkg.name }}</h3>
        <div class="package-diamonds">{{ pkg.diamonds_count }} 颗钻石</div>
        <div class="package-price">¥ {{ pkg.price.toFixed(2) }}</div>
        <div v-if="pkg.original_price" class="package-original-price">原价 ¥{{ pkg.original_price.toFixed(2) }}</div>
        <p class="package-description">{{ pkg.description || '解锁全部 AI 功能' }}</p>
        <el-button type="primary" round :loading="pkg.loading" @click.stop="createOrder(pkg)" class="purchase-button">立即购买</el-button>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * @description 钻石购买页面
 */
import api from '@/plugins/axios';

export default {
  name: 'Payment',
  data() {
    return {
      loading: true,
      packages: []
    };
  },
  created() {
    this.fetchPackages();
  },
  methods: {
    async fetchPackages() {
      this.loading = true;
      try {
        const { data } = await api.get('/payment/packages');
        this.packages = data.map(p => ({ ...p, loading: false }));
      } catch (error) {
        this.$message.error('获取钻石套餐失败');
      } finally {
        this.loading = false;
      }
    },
    selectPackage() {
      // Optional: add visual feedback on selection
    },
    async createOrder(pkg) {
      this.$set(pkg, 'loading', true);
      try {
        const { data } = await api.post('/payment/create', { package_id: pkg.id });
        this.$message.success('正在创建订单，即将跳转到支付页面...');
        // Redirect to payment gateway
        window.location.href = data.pay_url;
      } catch (error) {
        this.$message.error(error.response?.data?.detail || '创建订单失败');
      } finally {
        this.$set(pkg, 'loading', false);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.payment-page { padding-top: 40px; }
.page-header { text-align: center; margin-bottom: 40px; h1 { font-size: 28px; font-weight: 600; color: #fff; margin-bottom: 10px; } p { font-size: 16px; color: #9aa4b2; } }
.loading-state { height: 40vh; }

.packages-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
}

.package-card {
  background: #111419; border: 1px solid rgba(255, 255, 255, 0.1); border-radius: 16px; padding: 30px; text-align: center;
  position: relative; overflow: hidden; cursor: pointer; transition: all 0.3s;
  &:hover { transform: translateY(-10px); border-color: #3A7AFE; }
  &.recommended { border-color: #3A7AFE; box-shadow: 0 0 20px rgba(58, 122, 254, 0.3); }
}

.recommended-badge { position: absolute; top: 15px; right: -30px; background: #3A7AFE; color: #fff; padding: 5px 30px; transform: rotate(45deg); font-size: 12px; }
.package-icon { font-size: 40px; color: #3A7AFE; margin-bottom: 15px; }
.package-name { font-size: 20px; font-weight: 600; color: #e6e6e6; margin-bottom: 10px; }
.package-diamonds { font-size: 16px; color: #9aa4b2; margin-bottom: 15px; }
.package-price { font-size: 32px; font-weight: bold; color: #fff; margin-bottom: 5px; }
.package-original-price { font-size: 14px; color: #9aa4b2; text-decoration: line-through; margin-bottom: 20px; }
.package-description { font-size: 14px; color: #9aa4b2; min-height: 40px; margin-bottom: 25px; }
.purchase-button { width: 100%; }
</style>
